.burbuja {
  width: 60px;
  height: 60px;
  -webkit-clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
  clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
  z-index: 5;
  background: linear-gradient(45deg, #fff, #ecebeb);
  animation: estrella 8s linear infinite;
  position: absolute;
  bottom: 0;
  left: 100px;
  transform: rotate(360deg);
  box-shadow: 0 0 0 10px #f9d37a;
}

.burbuja:nth-child(2) {
  z-index: 5;
  animation: estrella 6s linear infinite;
  bottom: 0;
  left: 1400px;
}

.burbuja:nth-child(3) {
  z-index: 5;
  animation: estrella 4s linear infinite;
  bottom: 0;
  left: 1200px;
}

.burbuja:nth-child(4) {
  z-index: 5;
  animation: estrella 12s linear infinite;
  bottom: 0;
  left: 300px;
}

.burbuja:nth-child(6) {
  z-index: 5;
  animation: estrella 10s linear infinite;
  bottom: 0;
  left: 200px;
}

.burbuja:nth-child(7) {
  z-index: 5;
  animation: estrella 14s linear infinite;
  bottom: 0;
  left: 600px;
}

.burbuja:nth-child(8) {
  z-index: 5;
  animation: estrella 16s linear infinite;
  bottom: 0;
  left: 800px;
}

.burbuja:nth-child(9) {
  z-index: 5;
  animation: estrella 15s linear infinite;
  bottom: 0;
  left: 1000px;
}

.burbuja:nth-child(10) {
  z-index: 5;
  animation: estrella 18s linear infinite;
  bottom: 0;
  left: 400px;
}

.burbuja:nth-child(11) {
  z-index: 5;
  animation: estrella 16s linear infinite;
  bottom: 0;
  left: 50px;
}

.burbuja:nth-child(12) {
  z-index: 5;
  animation: estrella 18s linear infinite;
  bottom: 0;
  left: 1350px;
}

@keyframes estrella {
  0% {
    opacity: 0;
    bottom: 0;
    transform: translateY(800px)rotate(540deg);
  }

  50% {
    opacity: 4;
  }

  100% {
    opacity: 0;
    bottom: 100vh;
    transform: translateY(0)rotate(0deg);
  }
}

.header {
