  }
}

.backdrop {
  height: 100%;
  width: 100%;
  z-index: 10;
  visibility: visible;
  opacity: 1;
  background-color: rgba(0, 0, 0, .5);
  transition: opacity .5s ease-in;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  width: 485px;
  height: 630px;
  background-color: #fff;
  border-radius: 4px;
  padding: 40px;
  transition: transform .5s ease-in;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .3);
}

.is-hidden {
  visibility: visible;
  opacity: 0;
  pointer-events: none;
}

.is-hidden .modal {
  transform: translate(-50%, -50%)translateY(-100%);
}

.modal__btnX {
  width: 30px;
  height: 30px;
  cursor: pointer;
  background: #fff;
  border: 1px solid #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition-property: transform;
  transition-duration: .4s;
  transition-timing-function: ease-in;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__btnX:hover {
  color: #ffb8ca;
  border: 1px solid #ffb8ca;
  transform: rotate(360deg);
}

.modal__btnX:hover .modal__iconX {
  fill: #ffb8ca;
}

.modal__iconX:hover {
  cursor: pointer;
  fill: #ffb8ca;
}

.modal__data {
