.form__btn {
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #ffb8ca;
  z-index: 1;
  border: 3px solid #ffb8ca;
  border-radius: 2px;
  margin: 0 auto;
  padding: .7em 2em;
  font-family: DM Sans;
  font-size: 18px;
  font-weight: 700;
  transition: all .3s;
  display: block;
  position: relative;
  box-shadow: 0 2px 10px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .1);
}

.form__btn:before {
  content: "";
  opacity: 0;
  z-index: -1;
  background-color: #ffb8ca;
  transition: all .5s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  right: 50%;
}

.form__btn:hover, .form__btn:focus {
  color: #fff;
  cursor: pointer;
}

.form__btn:hover:before, .form__btn:focus:before {
  opacity: 1;
  transition: all .5s;
  left: 0;
  right: 0;
}

.form__btn:active {
